const octokit = new Octokit();


let listCreator = function () {

	let list = {
		quantity: 0,
		pageNumber: 1,
    per_page: 4,
    
    getData: function () {//получаем список данных по репозиториям

      const formatedResult = [];
      
			octokit.repos.listForUser({username: 'ChuVo', per_page: list.per_page, page: list.pageNumber}).then(result => {
				result.data.map(item => {
					const {
						name,
						html_url,
						description,
						language,
						forks,
						updated_at,
						stargazers_count
					} = item;
					
					formatedResult.push({
						name,
						html_url,
						description,
						language,
						forks,
						updated_at,
						stargazers_count
					})
				});
				list.addRepoItem (formatedResult);
      });
    },

    addRepoItem: function (repoList) {
      
			repoList.forEach(function (item) {
        
        const dateUp = item.updated_at,
              gitList = document.querySelector(".git__list"),
              gitElem = document.createElement('li'),
              gitName = document.createElement('a'),
              gitLang = document.createElement('span'),
              gitUpdate = document.createElement('span'),
              gitFork = document.createElement('div');


        gitElem.classList.add('list__elem');
        gitElem.appendChild( gitName );
        

        gitName.classList.add('list__title');
        gitName.innerHTML = item.name;
        gitName.href = item.html_url;

        gitLang.innerHTML = item.language;
        gitLang.classList.add('list__text');
        
        gitFork.classList.add('list__fork');

        gitList.appendChild( gitElem );

        if ( item.language ) {

          const langColor = document.createElement('div');
          langColor.classList.add('list__lang-color');

          gitElem.appendChild( gitLang );
          gitLang.appendChild( langColor );
          
          if  ( item.language === 'CSS' ) {
            langColor.classList.add('list__lang-css');
          }

          if ( item.language === 'JavaScript' ) {
            langColor.classList.add('list__lang-js');
          }

          if ( item.language === 'HTML' ) {
            langColor.classList.add('list__lang-html');
          }
        }

        if ( item.forks >= 0 ) {//выводит количество скачек, в итоговом варианте изменить на больше
          gitFork.innerHTML = item.forks;
          gitElem.appendChild( gitFork );
        }

        if ( item.stargazers_count ) {//выводит количество звезд

          const star = document.createElement('div');

          star.classList.add('list__star');
          star.innerHTML = item.stargazers_count;

          gitElem.appendChild( star );
        }

        gitUpdate.classList.add('list__data-update');
        gitUpdate.innerHTML = 'Updated on ' + getMonthNames();

        gitElem.appendChild( gitUpdate );

        //получение даты с прописным месяцем
        function getMonthNames() {

          const date = dateUp.substring(0, 10).split('-');

          if ( date[1] === '01') {
            date[1] = "January";
          } else if ( date[1] === '02') {
            date[1] = "February";
          } else if ( date[1] === '03') {
            date[1] = "March";
          } else if ( date[1] === '04') {
            date[1] = "April";
          } else if ( date[1] === '05') {
            date[1] = "May";
          } else if ( date[1] === '06') {
            date[1] = "June";
          } else if ( date[1] === '07') {
            date[1] = "July";
          } else if ( date[1] === '08') {
            date[1] = "August";
          } else if ( date[1] === '09') {
            date[1] = "September";
          } else if ( date[1] === '10') {
            date[1] = "October";
          } else if ( date[1] === '11') {
            date[1] = "November";
          } else if ( date[1] === '12') {
            date[1] = "December";
          }
          
          let newDate = date.reverse().join(' ');

          return newDate;
        }
      })
    },

    paginator: function() {
      octokit.repos.listForUser( {username:'ChuVo'} ).then(result => {

        const count = result.data.length,//общее колво элементов list.quantity
              cnt = list.per_page,//сколько отображаю на странице репо list.per_page
              cnt_page = Math.ceil(count / cnt),//кол-во страниц


              navPage = document.createElement( 'nav' ),
              gitBox = document.querySelector( '.git__list-wrapper'),
              gitList = document.querySelector('.git__list'),
              preloader = document.querySelector('.git__preloader');

        let pageNum = '';

        navPage.classList.add( 'list__nav' );
        gitBox.appendChild( navPage );
        
        for ( let i = 0; i < cnt_page; i++ ) {
          pageNum += "<button class='list__nav-page' data-page=" + i * cnt + "  id=\"page" + (i + 1) + "\">" + (i + 1) + "</button>";
        }       

        navPage.innerHTML =  pageNum;

        function pageBtnAction() {
          const pageBtn = document.querySelectorAll('.list__nav-page');

          pageBtn.forEach( (i) => {

            i.addEventListener( 'click', (e) => {
              makeBtnNoActive();
              

              e.target.classList.add('list__nav-page--active');

              list.pageNumber = e.target.innerHTML;
              
              showNextGits();
              
            });
          });
        }

        function showNextGits() {
          
          while (gitList.firstChild) {
            gitList.removeChild(gitList.firstChild);
          }
          showGitPreloader();
          list.getData();showGitPreloader();
        }

        function makeBtnNoActive() {
          const pageBtn = document.querySelectorAll('.list__nav-page');

          pageBtn.forEach( (i) => {
            if  ( i.classList.contains('list__nav-page--active') ) {
             i.classList.remove('list__nav-page--active');
           }
          });          
        }

        function showGitPreloader() {
         
          if ( gitList.children.length === 0) {
            preloader.style.display = 'flex';
          } 
          setTimeout( () => { 
            preloader.style.display = 'none';
          }, 1500);
        }

        pageBtnAction();   
      })
      list.getData();
    }    
  }
  return list.paginator();

}


listCreator();
